html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  color: #1a1b15;
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
  height: 100%;
}
@media (max-width: 480px) {
  body {
    min-height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: min(95%, 120rem);
  margin: 0 auto;
}

.contenedor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

/*Componenetes*/
.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta__error {
  background-color: rgba(0, 0, 0, 0.5);
  border-left: 0.5rem solid #FE0000;
  color: #FE0000;
}
.alerta__exito {
  background-color: rgba(0, 0, 0, 0.5);
  border-left: 0.5rem solid #02db02;
  color: #02db02;
}

.divAlertas {
  margin: 1rem auto;
}

.boton-azul {
  background-color: #003561;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-azul {
    width: 100%;
  }
}
.boton-azul:hover {
  background-color: #00192e;
  cursor: pointer;
}
.boton-azul:hover {
  background-color: black;
}

.boton-gris {
  background-color: #076493;
  padding: 1.5rem 4rem;
  color: #64748B;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-gris {
    width: 100%;
  }
}
.boton-gris:hover {
  background-color: #054362;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #FE0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-eliminar {
    width: 100%;
  }
}
.boton-eliminar:hover {
  background-color: #cb0000;
  cursor: pointer;
}

.boton-cerrar {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  border: 1px solid #FFFFFF;
}
@media (max-width: 768px) {
  .boton-cerrar {
    width: 100%;
  }
}
.boton-cerrar:hover {
  background-color: black;
  cursor: pointer;
}
.boton-cerrar:hover {
  background-color: #36382b;
}

.swal2-popup {
  font-size: 1.6rem !important;
  font-family: Georgia, serif;
}

.news {
  background-color: hsl(198, 70%, 5%);
  background-image: radial-gradient(at 0% 0%, hsl(192, 41%, 4%) 0px, transparent 50%), radial-gradient(at 84% 86%, hsl(198, 70%, 5%) 0px, transparent 50%), radial-gradient(at 47% 17%, hsl(199, 68%, 6%) 0px, transparent 50%), radial-gradient(at 69% 33%, hsl(196, 40%, 5%) 0px, transparent 50%), radial-gradient(at 55% 66%, hsl(199, 73%, 5%) 0px, transparent 50%), radial-gradient(at 25% 93%, hsl(192, 40%, 4%) 0px, transparent 50%), radial-gradient(at 92% 7%, hsl(198, 70%, 5%) 0px, transparent 50%);
}
.news__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: 3fr 2fr;
  margin-top: 8rem;
  align-items: center;
}
@media (max-width: 480px) {
  .news__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.news__iframe {
  width: 100%;
  height: 30rem;
}
.news__info {
  text-align: center;
}
.news__info h3 {
  color: #FFFFFF;
}
.news__info ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.news__info li {
  margin: 2rem 0;
  color: #b3b3b3;
}

.team__grid {
  display: grid;
  gap: 5rem;
  grid-template-columns: 2fr 3fr;
  align-items: center;
}
@media (max-width: 480px) {
  .team__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.team__img {
  height: 60%;
  position: relative;
}
.team__bg img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.team__foto {
  position: absolute;
  bottom: -40px; /* Ajusta esta posición según sea necesario */
  left: 2.5rem;
  width: 9rem;
  height: 9rem;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #FFFFFF;
}
.team__foto img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}
.team__estudiante {
  padding: 5rem 1rem 3rem 1rem;
  font-size: 1.7rem;
  color: #b3b3b3;
  font-weight: 700;
  text-align: center;
}
.team__orcid {
  color: #FFFFFF;
}
.team__info {
  margin: 2rem;
  padding: 2rem;
  color: #b3b3b3;
  background-color: #1a1b15;
  border-radius: 1rem;
}
.team__info p {
  color: #FFFFFF;
  font-size: 1.6rem;
}

.mySwiper {
  margin: 5rem auto;
  width: calc(100% - 5rem);
  height: 40rem;
  background-color: #1a1b15;
  border-radius: 1rem;
}
@media (max-width: 480px) {
  .mySwiper {
    margin: 0 auto 5rem 0;
  }
}

/*UI*/
.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
  position: relative;
}
.formulario__campo--encima {
  flex-direction: column;
}
.formulario__label {
  flex: 0 0 10rem;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__label--encima {
  align-self: flex-start;
  flex: 0 0 4rem;
}
.formulario__input {
  padding: 1rem;
  border: none;
  width: 100%;
  outline: none;
  background-color: rgba(0, 0, 1, 0.5);
  color: #FFFFFF;
}
.formulario__input--file {
  border: none;
}
.formulario__input--sociales {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__input--textarea {
  resize: none;
  height: 10rem;
}
.formulario__radio {
  display: flex;
  width: 100%;
  gap: 2rem;
}
.formulario__submit {
  background-color: #003561;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  width: auto;
  align-self: flex-start;
}
@media (max-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__submit:hover {
  background-color: #00192e;
  cursor: pointer;
}
.formulario__submit--registrar {
  margin: 1rem 0 0 0;
  width: 100%;
}
.formulario__icon {
  position: absolute;
  right: 0;
  margin-right: 0.5rem;
  cursor: pointer;
}
.formulario__contenedor--icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
  margin-top: 1rem;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #003561;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
  cursor: pointer;
}
.formulario__imagen img {
  max-width: 20rem;
}
.formulario__texto {
  margin-top: 3rem;
}

.acciones, .acciones--centrar {
  margin: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlaces {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}
.acciones--centrar {
  justify-content: center;
}

.horas {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 480px) {
  .horas {
    display: block;
  }
}
.horas__hora {
  color: #003561;
  border: 1px solid #003561;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
}
.horas__hora--seleccionado {
  background-color: #003561;
  color: #FFFFFF;
  font-weight: #1a1b15;
}
.horas__hora--deshabilitado {
  cursor: not-allowed;
  border: 1px solid #64748B;
  color: #64748B;
  opacity: 0.5;
}

.header {
  min-height: 30rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
@media (max-width: 480px) {
  .header {
    height: auto;
  }
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #003561, #00c8c2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (max-width: 480px) {
  .header__logo {
    text-align: center;
  }
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 1rem;
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
}
@media (max-width: 480px) {
  .header__navegacion {
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }
}
.header__enlace, .header__submit {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: color 0.3s ease;
  background-color: transparent;
  border: none;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}
.header__enlace:hover, .header__submit:hover {
  color: #003561;
}
.header__user {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}
.header__texto {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
  margin: 2rem 0;
  text-transform: uppercase;
}
@media (max-width: 480px) {
  .header__texto {
    text-align: center;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  display: inline-block;
  text-transform: uppercase;
  margin: 2rem 0;
}
@media (max-width: 768px) {
  .header__boton {
    width: 100%;
  }
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (max-width: 480px) {
  .header__boton {
    display: block;
  }
}
.header__form {
  display: flex;
  justify-content: center;
}
.barra {
  background-color: #003561;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.barra__logo {
  color: #FFFFFF;
  text-align: left;
  margin: 0;
  white-space: nowrap;
}
@media (max-width: 480px) {
  .barra__logo {
    text-align: center;
  }
}

.navegacion {
  display: flex;
  align-items: center;
  padding: 2rem;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
@media (max-width: 768px) {
  .navegacion {
    flex-direction: column;
    text-align: center;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .navegacion {
    flex-direction: column;
    text-align: center;
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 1.8rem;
  padding: 2rem;
  font-weight: 900;
  text-transform: uppercase;
  transition: 0.3s ease;
  white-space: nowrap;
}
.navegacion__enlace:hover, .navegacion__enlace--fijo {
  background-color: #FFFFFF;
  color: #003561;
}
@media (max-width: 480px) {
  .navegacion__enlace {
    padding: 2rem;
  }
}

.footer {
  background-color: #003561;
  padding-top: 3rem;
  color: #FFFFFF;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 768px) {
  .footer__grid {
    display: block;
  }
}
.footer__logo {
